*{
    font-family: 'cour';
    color: #f9ce00;
}
body{
    height: 100vh;
}
.t{
    width: 100%;
    height: 100vh;
    padding: 100px 0 0 0;
    position: relative;
    background: url("../images/test-bg.jpg");
    background-position: center center;
    background-size: 100% 100%;
    background-clip: cover;
    overflow: hidden;
    h1{
        text-align: center;
        font-size: 50px;
    }
    .t-BigBox{
        width: 100%;
        height: 80vh;
        position: relative;
        text-align: center;
        overflow-y: scroll;
        display: grid;
        justify-content: center;
        .t-box{
            width: 400px;
            height: 475px;
            background: #fff;
            border-radius: 10px;
            position: relative;
            margin-bottom: 30px;
            .t-box-img{
                width: 400px;
                border-radius: 10px;
            }
            .t-box-text{
                padding-top: 20px;
                *{
                    text-align: center;
                    margin: auto 0;
                    color: #1e392a;
                }
                h2{
                    font-size: 35px;
                }
                h3{
                    font-size: 30px;
                    text-transform: capitalize;
                }
                img{
                    width: 50px;
                    height: 50px;
                    position: absolute;
                    bottom: 15px;
                    right: 15px;
                }
            }
        }
    }
}
@media(max-width:830px){
    .t{
        h1{
            font-size: 30px;
        }
        .t-BigBox{
            .t-box{
                width: 350px;
                height: 475px;
                margin-bottom: 30px;
                .t-box-img{
                    width: 350px;
                }           
        }
    }
    }
}