// Font setup
@font-face {
    font-family: 'consolas';
    src: url(./fonts/consolas.ttf);
}
@font-face {
    font-family: 'cour';
    src: url(./fonts/cour.ttf);
}
@font-face {
    font-family: 'dubai';
    src: url(./fonts/dubai.TTF);
}
@font-face {
    font-family: 'gabriola';
    src: url(./fonts/Gabriola.ttf);
}
// Font setup

*{
    margin: 0;
    padding: 0;
    transition: 0.8s;
    scroll-behavior: smooth;
}
::selection{
    background: transparent;
    color: #56a714;
    font-family: 'gabriola';
}
body{
    overflow: hidden;
    position: relative;
}
.nav{
    width: 100%;
    z-index: 999;
    // padding: ;   
    display: flex;
    justify-content: center ;
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    background: rgba(0, 0, 0,0.75);
    .logo{
        position: relative;
        font-size: 35px;
        font-family: 'cour';
        font-weight: 900;
        margin-top:20px ;
        a{
            text-decoration: none;
            color: #67db08;
        }
    }
    .menu-box{
        img{
            width: 40px;
            height:40px;
            z-index: 1000;
        }
        .open{
            position: absolute;
            top: 10px;
            left:  20px;
        }
    }
    .close{
        position: absolute;
        top: 10px;
        right: 20px;
        width: 40px;
        height:40px;
        display: none;
    }
    ul{
        position: fixed;
        left: -300px;
        top: 0;
        bottom: 0;
        background: rgba(0, 0, 0, 0.7001);
        border-top-right-radius:20px;
        border-bottom-right-radius:20px;
        display: grid;
        place-items: center;
        width: 300px;
        z-index: 100;
        
        box-shadow: 5px -5px 5px #000;
        li{
            list-style: none;
            font: 22px 'cour';
            a{
                color: #ff0;
                text-decoration: none;
            }
        }
    }
}
@media(max-width:830px){
    .nav{
        .logo{
            a{
                display: block;
                font-size: 25px;
                padding-bottom: 10px;
            }
        }
    }
}