*{
    margin: 0;
    padding: 0;
}
.c{
    width: 100%;
    height: 110vh;
    overflow: hidden;
    padding-top: -170px;
    display: flex;
    background: url('../images/contact-bg.jpg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: 100% 100%;
    background-color: #000;
    position: relative;
    .c-one{
        width: 50%;
        height: 100vh;
        padding-top: 250px;
        padding-left: 50px;
        position: relative;
        h1{
            font-size: 50px;
            font-family: 'gabriola';
            font-weight: 500;
            line-height: 50px;
            letter-spacing: 5px;
        }
        .c-l{
            margin-top: 30px;
            width: 100%;
            span{
                display: block;
                font-size: 30px;
                margin-top: 20px;
            }
        }
    }
    .c-two-box{
        height: 100vh;
        position: relative;
        display: grid;
        place-items: center;
        width: 50%;
        .c-two{
            width: 350px;
            height: 500px;
            position: relative;
            background: #fff;
            border-radius: 30px;
            padding: 40px 20px;
            h1{
                font-family:'gabriola' ;
                letter-spacing: 3px;
            }
            .c-two-input-name{
                width: 100%;
                display: flex;
                justify-content: space-between;
                input{
                    width: 150px;
                    height: 30px;
                    border: none;
                    border-bottom: 2px solid black;
                    color: darkred;
                }
            }
            .c-two-input{
                position: relative;
                input{
                    width: 150px;
                    height: 30px;
                    border: none;
                    border-bottom: 2px solid black;
                    color: darkred;
                    margin-top: 30px;
                    margin-bottom: 30px;
                    font-weight: 500;
                    &:active{
                        border: none;
                        border-bottom: 2px solid black;
                    }
                }
                textarea{
                    border: 2px solid black;
                    padding: 3px 5px;
                    color: darkred;
                }
                button{
                    border: 2px solid black;
                    padding: 10px 20px;
                    position: absolute;
                    right: 0;
                    color:#1e392a;
                    font-weight: 600;
                    background:#fff;
                }
            }
        }
    }
}
@media (max-width:850px) {
    body{
        height: 110vh;
        overflow: auto;
        position: relative;
    }
    .c{
        position: relative;
        overflow: auto;
        padding: 0;
        display: block;
        height:120vh;
        .c-one{
            width: 100%;
            height: 60vh;
            h1{
                width: 90%;           
            }
            .c-l{
                span{
                    width: 80%;
                }
            }
        }
        .c-two-box{
            position: relative;
            height: 50vh;
            margin-bottom: 20px;
            .c-two{
                position: relative;   
                bottom: 0;
                left: 0;
                right: 0;
                .c-two-input{
                    button{
                        position: absolute;
                        bottom: -10px;
                        right: 9px;
                    }
                }
            }
        }
    }
}