*{
    margin: 0;
    padding: 0;
}
body{
    height: 100vh;
}
.error-page{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100vh;
    display: grid;
    z-index: 1000;
    background: #000;
    place-items: center;
    font-family: 'gabriola';
    font-size: 40px;
}