*{
    margin: 0;
    padding: 0;
    align-items: center;
    text-align: center;
    color: #5c9f24;
}
body{
    height: 100vh;
}
.h{
    width: 100%;
    height: 100vh;
    position: relative;
    padding: 150px 0px  0 0;
    background: url("../images/bg-main.jpg") center center;
    background-size: cover;
    
    .h-top{
        width: 100%;
        height: 50%;
        display: flex;
        text-align: center;
        align-items: center;
        justify-content: space-around;
        h1{
            font: 30px 'dubai';
        }
        span{
            font: 27px 'gabriola';
            color:#f2f3f3;
            margin-bottom: 10px;
        }
        .h-top-img{
            width: 550px;
            height: 300px;
            border-radius: 10px;
        }
    }
    .h-bottom{
        position: relative;
        bottom: 0;
        right: 0;
        left: 0;
        width: 100%;
        height: 360px;
        margin-top: 50px;
        background: url(../images/footermain.jpg) no-repeat center 100%;
        // background-attachment: fixed;
        // .glitchBox{
            //     position: absolute;
            //     background: url(../images/footermain.jpg) no-repeat center 100%;
            //     background-attachment: fixed;
            // }
    }
    
}
@media(max-width:830px){
    .h{
        .h-top{
            display: grid;
            text-align: center;
            width: 100%;
            h1{
                    text-align: center;
                    font-size: 23px ;
                    width: 100%;
                }
                span{
                    text-align: center;
                    font-size: 25px ;
                    padding-left: 50px ;
                }
            .h-top-img{
                display: none;
            }
        }
        .h-bottom{
            position: absolute;
            bottom: 0;
        }
    }
}
@media(max-width:500px){
    .h{
        
        .h-top{
            display: grid;
            .h-w{
                text-align: center;
                margin: 0 auto;
                span{
                    margin: 0 30px;
                    text-align: center!important;
                }
            }

            .h-top-img{
                display: none;
            }
        }
        .h-bottom{
            position: absolute;
            bottom: 0;
            background-position: center;
            height: 350px;
        }
    }
}