*{
    text-align: start;
    margin: 0;
    padding: 0;
}
body{
    height: 100vh;
}
.l{
    width: 100%;
    position: relative;
    height: 100vh;
    background: url(../images/bg-location.jpg);
    background-color: #000;
    background-repeat: no-repeat;
    background-size: 100% 100%;
    background-attachment: fixed;
    padding-top: 70px;
    padding-left:70px ;
    padding-right:70px ;
    display: flex;
    justify-content: space-between;

    .l-one{
        width: 40%;
        display: grid;
        padding-left: 50px;
        .l-one-about{
            font-family: 'cour';
            font-size: 30px;
            letter-spacing: 2.5px;
            font-weight: 700;
            display: flex;
            margin-top: 20px;
            span{
                color: #000!important;
            }
            .l-anim{
                border-right: 7px #fff solid;
                margin-left: 15px;
                // width: 250px;
                overflow: hidden;
                transition: 1.5s;
                animation: locationanimation 3s infinite;
            }
        }
    }
    .l-two{
        width: 40%;
        position: relative;
        display: grid;
        place-items: center;
        .l-img{
            width: 450px;
            margin-right: 150px;
            filter:hue-rotate(270deg);
            border-radius: 15px;
            &:hover{
                transform: scale(3) translateX(-100px) translateY(-10px);
            }
        }
        
    }
}
@keyframes locationanimation {
    0%,100%{
        width: auto;
    }
    50%{
        width: 0.1px;
    }
    
}
@media(max-width:830px){
   .l{
       overflow: hidden;
       background: #ff0;
       display: contents;
       .l-one{
           padding: 100px 0 0 0;    
           width: 100%;
           overflow: hidden;
           height: 50vh;
           background: linear-gradient(350deg , #1FA2FF,#12D8FA,#A6FFCB);
           .l-one-about{
                span{
                    font-size: 20px;
                    padding-left:20px ;
                }
            }           
            width: 100%;
       }
       .l-two{
           position: relative;
           height: 50vh;
           width: 100%;
           .l-img{
               position: absolute;
               bottom: 0;   
               right: 0;
               left: 0;
               width: 100%;
            //    width: 100%;
               height:50vh;
            }
       }
   }
}